//  '/'  '@'
export const changeStr = (str) => {
  let result = '';
  for (let i = 0; i < str.length; i++) {
    if(str[i] != '/') result += str[i];
    else result += '@';
  }
  
  return result;
};

// ()
const pathChars = /[ !"#$&'()*,:;<=>?@[\\\]^`{}~]/g;
export const escapePath = (str) => {
  return str.replace(pathChars, function(match) {
    return '\\' + match;
  });
};

// ()
export const escapeItem = (str) => {
  return escapePath(str);
};